export class APIKey {
  private _key: string;
  private _room: string;

  constructor(key: string) {
    this._key = key;
    if (this.checkKey(key)) {
      this._room = this.getRoomID();
    } else {
      this._room = "";
    }
  }

  public isValid(): boolean {
    return this.checkKey(this._key);
  }
  public getRoomID(): string {
    return this._key;
  }
  private checkKey(key: string): boolean {
    return true;
  }
}
