import {
  SimpleEventDispatcher,
  ISimpleEvent,
  ISimpleEventHandler
} from "strongly-typed-events";
import { Joystick, UserClient } from "../models";

export default class EventManager {
  private _onJoystickMove = new SimpleEventDispatcher<Joystick>();
  private _onMoveUp = new SimpleEventDispatcher<Joystick>();
  private _onMoveRigth = new SimpleEventDispatcher<Joystick>();
  private _onMoveDown = new SimpleEventDispatcher<Joystick>();
  private _onMoveLeft = new SimpleEventDispatcher<Joystick>();
  private _onNewClient = new SimpleEventDispatcher<UserClient>();

  constructor() {
    // just do nothing at the moment
  }

  // Subscribable Events
  public onMoveUp(): ISimpleEvent<Joystick> {
    return this._onMoveUp.asEvent();
  }
  public get onJoystickMove(): ISimpleEvent<Joystick> {
    return this._onJoystickMove.asEvent();
  }
  public get onNewClient(): ISimpleEvent<UserClient> {
    return this._onNewClient.asEvent();
  }
  public get onMoveRight(): ISimpleEvent<Joystick> {
    return this._onMoveRigth.asEvent();
  }
  public get onMoveLeft(): ISimpleEvent<Joystick> {
    return this._onMoveLeft.asEvent();
  }
  public get onMoveDown(): ISimpleEvent<Joystick> {
    return this._onMoveDown.asEvent();
  }

  // Dispatch Methods
  public dispatchJoystickMove(joystick: Joystick) {
    this._onJoystickMove.dispatch(joystick);
  }
  public dispatchJoystickUp(joystick: Joystick) {
    this._onMoveUp.dispatch(joystick);
  }
  public dispatchJoystickRight(joystick: Joystick) {
    this._onMoveRigth.dispatch(joystick);
  }
  public dispatchJoystickDown(joystick: Joystick) {
    this._onMoveDown.dispatch(joystick);
  }
  public dispatchJoystickLeft(joystick: Joystick) {
    this._onMoveLeft.dispatch(joystick);
  }
  public dispatchNewClient(userClient: UserClient) {
    this._onNewClient.dispatch(userClient);
  }
}
