import * as socketio_ from "socket.io-client";
import ConnectionManager from "./connection-manager";
import { Joystick, UserClient } from "../models";
export default class Connection {
  private _socket: SocketIOClient.Socket;
  private _connectionManger: ConnectionManager;
  private roomId: string;

  constructor(connectionManger: ConnectionManager) {
    this._connectionManger = connectionManger;
    this.roomId = "";
    let socketio: any = (socketio_ as any).default || socketio_;
    this._socket = socketio("https://ottoheroku.herokuapp.com");
  }

  public createRoom(roomId: string) {
    this._socket.emit("create", roomId);
    this.listen(this._socket);
  }

  private listen(socket: SocketIOClient.Socket) {
    
    let self = this;
    socket.on("newUser", function(userId: string) {
      let user = new UserClient(userId);
      self._connectionManger.eventmanager.dispatchNewClient(user);
    });
    socket.on("jsm", function(joystickData:any){
      let joystick = new Joystick(Number(joystickData.sAngle), Number(joystickData.sDistance))
      self._connectionManger.eventmanager.dispatchJoystickMove(joystick)
    });
    socket.on("jss", (data: any) => console.log(data));
  }
}
