import ConnectionManager from './connection/connection-manager'
import { Button, Joystick, UserClient } from './models'
import Factory from './utils/factory'
import { ISimpleEvent } from "strongly-typed-events";
import EventManager from './events/event-manager';
import { APIKey } from './models/api-Key';
export default class OttoLib {
  public onJoystickMove!: ISimpleEvent<Joystick>
  public newUserJoined!: ISimpleEvent<UserClient>
  private connectionManager!: ConnectionManager
  private eventManager!: EventManager
  private key: APIKey;

  constructor(appKey: string) {
    this.key = new APIKey(appKey);
    if (this.key.isValid()){
        this.eventManager = Factory.createEventManager();
        this.connectionManager = Factory.createConnectionManager(this.key.getRoomID(), this.eventManager)
        this.initEvent();
    }
  }
  public start(roomID: string) {
    this.connectionManager.createRoom(roomID)
  }
  
  private initEvent(){
      // add more Events hier
      this.onJoystickMove = this.eventManager.onJoystickMove;
      this.newUserJoined = this.eventManager.onNewClient;
  }
  
}
